import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { 
  Search, 
  Filter, 
  BookOpen, 
  Video, 
  FileText, 
  CheckSquare,
  Clock,
  User,
  Star,
  Bookmark,
  BookmarkCheck,
  TrendingUp,
  Users,
  DollarSign,
  Scale,
  Heart,
  Zap
} from 'lucide-react';
import GlassCard from '../components/UI/GlassCard';
import Button from '../components/UI/Button';
import LoadingSpinner from '../components/UI/LoadingSpinner';
import { supabase } from '../lib/supabase';

const Resources = () => {
  const [resources, setResources] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('All');
  const [selectedStage, setSelectedStage] = useState('All');
  const [selectedType, setSelectedType] = useState('All');
  const [bookmarkedResources, setBookmarkedResources] = useState(new Set());

  const categories = ['All', 'Fundraising', 'Product Development', 'Legal', 'Marketing', 'Operations', 'Leadership'];
  const stages = ['All', 'Idea', 'MVP', 'Seed', 'Growth', 'Scale'];
  const types = ['All', 'Article', 'Video', 'Checklist', 'Case Study', 'Template'];

  const categoryIcons = {
    'Fundraising': DollarSign,
    'Product Development': Zap,
    'Legal': Scale,
    'Marketing': TrendingUp,
    'Operations': Users,
    'Leadership': Heart
  };

  useEffect(() => {
    fetchResources();
  }, []);

  const fetchResources = async () => {
    try {
      const { data, error } = await supabase.from('resources').select('*');
      if (!error) {
        setResources(data);
      }
    } catch (error) {
      console.error('Error fetching resources:', error);
    } finally {
      setLoading(false);
    }
  };

  const filteredResources = resources.filter(resource => {
    const matchesSearch = resource.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         resource.description.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesCategory = selectedCategory === 'All' || resource.category === selectedCategory;
    const matchesStage = selectedStage === 'All' || resource.stage === selectedStage;
    const matchesType = selectedType === 'All' || resource.type === selectedType;
    
    return matchesSearch && matchesCategory && matchesStage && matchesType;
  });

  const featuredResources = resources.filter(resource => resource.featured);

  const toggleBookmark = (resourceId) => {
    setBookmarkedResources(prev => {
      const newSet = new Set(prev);
      if (newSet.has(resourceId)) {
        newSet.delete(resourceId);
      } else {
        newSet.add(resourceId);
      }
      return newSet;
    });
  };

  const getTypeIcon = (type) => {
    switch (type) {
      case 'Article': return FileText;
      case 'Video': return Video;
      case 'Checklist': return CheckSquare;
      case 'Case Study': return BookOpen;
      default: return FileText;
    }
  };

  const getTypeColor = (type) => {
    switch (type) {
      case 'Article': return 'text-blue-400 bg-blue-500/20';
      case 'Video': return 'text-red-400 bg-red-500/20';
      case 'Checklist': return 'text-green-400 bg-green-500/20';
      case 'Case Study': return 'text-purple-400 bg-purple-500/20';
      default: return 'text-gray-400 bg-gray-500/20';
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen pt-20 flex items-center justify-center">
        <LoadingSpinner size="xl" />
      </div>
    );
  }

  return (
    <div className="min-h-screen pt-20">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          className="text-center mb-12"
        >
          <h1 className="text-4xl md:text-6xl font-bold mb-6">
            <span className="gradient-text">Resource</span>
            <br />
            <span className="text-white">Library</span>
          </h1>
          <p className="text-xl text-gray-300 max-w-3xl mx-auto">
            Access curated startup guides, case studies, templates, and expert insights 
            to help you navigate every stage of your entrepreneurial journey.
          </p>
        </motion.div>

        {/* Featured Resources */}
        {featuredResources.length > 0 && (
          <div className="mb-12">
            <h2 className="text-2xl font-bold text-white mb-6 flex items-center">
              <Star className="h-6 w-6 mr-2 text-yellow-400" />
              Featured Resources
            </h2>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {featuredResources.slice(0, 3).map((resource, index) => {
                const TypeIcon = getTypeIcon(resource.type);
                const CategoryIcon = categoryIcons[resource.category] || BookOpen;
                const isBookmarked = bookmarkedResources.has(resource.id);
                
                return (
                  <motion.div
                    key={resource.id}
                    initial={{ opacity: 0, y: 50 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: index * 0.1 }}
                  >
                    <GlassCard className="h-full relative group cursor-pointer hover:bg-gradient-to-br hover:from-accent-blue/5 hover:to-accent-purple/5">
                      {/* Bookmark Button */}
                      <button
                        onClick={(e) => {
                          e.stopPropagation();
                          toggleBookmark(resource.id);
                        }}
                        className="absolute top-4 right-4 p-2 rounded-lg hover:bg-white/10 transition-colors duration-300 z-10"
                      >
                        {isBookmarked ? (
                          <BookmarkCheck className="h-5 w-5 text-accent-blue" />
                        ) : (
                          <Bookmark className="h-5 w-5 text-gray-400 hover:text-white" />
                        )}
                      </button>

                      {/* Featured Badge */}
                      <div className="absolute top-4 left-4 px-2 py-1 bg-gradient-to-r from-yellow-500 to-orange-500 text-white text-xs rounded-full flex items-center space-x-1">
                        <Star className="h-3 w-3" />
                        <span>Featured</span>
                      </div>

                      <div className="pt-12">
                        <div className="flex items-center space-x-3 mb-4">
                          <div className="p-2 rounded-lg bg-gradient-to-r from-accent-blue to-accent-purple">
                            <CategoryIcon className="h-5 w-5 text-white" />
                          </div>
                          <div className="flex items-center space-x-2">
                            <span className={`px-2 py-1 text-xs rounded-full flex items-center space-x-1 ${getTypeColor(resource.type)}`}>
                              <TypeIcon className="h-3 w-3" />
                              <span>{resource.type}</span>
                            </span>
                            <span className="px-2 py-1 bg-accent-purple/20 text-accent-purple text-xs rounded-full">
                              {resource.stage}
                            </span>
                          </div>
                        </div>

                        <h3 className="text-lg font-semibold text-white mb-2 group-hover:text-accent-blue transition-colors duration-300">
                          {resource.title}
                        </h3>

                        <p className="text-gray-400 text-sm mb-4 line-clamp-2">
                          {resource.description}
                        </p>

                        <div className="flex items-center justify-between text-sm text-gray-400 mb-4">
                          <div className="flex items-center space-x-1">
                            <User className="h-4 w-4" />
                            <span>{resource.author}</span>
                          </div>
                          <div className="flex items-center space-x-1">
                            <Clock className="h-4 w-4" />
                            <span>{resource.readTime}</span>
                          </div>
                        </div>

                        <Button size="sm" className="w-full">
                          Read More
                        </Button>
                      </div>
                    </GlassCard>
                  </motion.div>
                );
              })}
            </div>
          </div>
        )}

        {/* Search and Filters */}
        <GlassCard className="mb-8">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
            {/* Search */}
            <div className="lg:col-span-2 relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
              <input
                type="text"
                placeholder="Search resources..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-3 form-input rounded-lg"
              />
            </div>

            {/* Category Filter */}
            <select
              value={selectedCategory}
              onChange={(e) => setSelectedCategory(e.target.value)}
              className="px-4 py-3 form-input rounded-lg"
            >
              {categories.map(category => (
                <option key={category} value={category}>{category}</option>
              ))}
            </select>

            {/* Stage Filter */}
            <select
              value={selectedStage}
              onChange={(e) => setSelectedStage(e.target.value)}
              className="px-4 py-3 form-input rounded-lg"
            >
              {stages.map(stage => (
                <option key={stage} value={stage}>{stage}</option>
              ))}
            </select>

            {/* Type Filter */}
            <select
              value={selectedType}
              onChange={(e) => setSelectedType(e.target.value)}
              className="px-4 py-3 form-input rounded-lg"
            >
              {types.map(type => (
                <option key={type} value={type}>{type}</option>
              ))}
            </select>
          </div>
        </GlassCard>

        {/* Resources Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredResources.map((resource, index) => {
            const TypeIcon = getTypeIcon(resource.type);
            const CategoryIcon = categoryIcons[resource.category] || BookOpen;
            const isBookmarked = bookmarkedResources.has(resource.id);
            
            return (
              <motion.div
                key={resource.id}
                initial={{ opacity: 0, y: 50 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: index * 0.1 }}
              >
                <GlassCard className="h-full relative group cursor-pointer">
                  {/* Bookmark Button */}
                  <button
                    onClick={(e) => {
                      e.stopPropagation();
                      toggleBookmark(resource.id);
                    }}
                    className="absolute top-4 right-4 p-2 rounded-lg hover:bg-white/10 transition-colors duration-300"
                  >
                    {isBookmarked ? (
                      <BookmarkCheck className="h-5 w-5 text-accent-blue" />
                    ) : (
                      <Bookmark className="h-5 w-5 text-gray-400 hover:text-white" />
                    )}
                  </button>

                  <div className="flex items-center space-x-3 mb-4">
                    <div className="p-2 rounded-lg bg-gradient-to-r from-accent-blue to-accent-purple">
                      <CategoryIcon className="h-5 w-5 text-white" />
                    </div>
                    <div className="flex items-center space-x-2">
                      <span className={`px-2 py-1 text-xs rounded-full flex items-center space-x-1 ${getTypeColor(resource.type)}`}>
                        <TypeIcon className="h-3 w-3" />
                        <span>{resource.type}</span>
                      </span>
                      <span className="px-2 py-1 bg-accent-purple/20 text-accent-purple text-xs rounded-full">
                        {resource.stage}
                      </span>
                    </div>
                  </div>

                  <h3 className="text-lg font-semibold text-white mb-2 group-hover:text-accent-blue transition-colors duration-300 pr-8">
                    {resource.title}
                  </h3>

                  <p className="text-gray-400 text-sm mb-4 line-clamp-3">
                    {resource.description}
                  </p>

                  <div className="flex items-center justify-between text-sm text-gray-400 mb-4">
                    <div className="flex items-center space-x-1">
                      <User className="h-4 w-4" />
                      <span>{resource.author}</span>
                    </div>
                    <div className="flex items-center space-x-1">
                      <Clock className="h-4 w-4" />
                      <span>{resource.readTime}</span>
                    </div>
                  </div>

                  {/* Tags */}
                  <div className="flex flex-wrap gap-2 mb-4">
                    {resource.tags.slice(0, 3).map((tag, tagIndex) => (
                      <span
                        key={tagIndex}
                        className="text-xs px-2 py-1 bg-white/10 text-gray-300 rounded-full"
                      >
                        {tag}
                      </span>
                    ))}
                  </div>

                  <Button size="sm" className="w-full">
                    Read More
                  </Button>
                </GlassCard>
              </motion.div>
            );
          })}
        </div>

        {filteredResources.length === 0 && (
          <div className="text-center py-12">
            <BookOpen className="h-16 w-16 text-gray-400 mx-auto mb-4" />
            <h3 className="text-xl font-semibold text-white mb-2">
              No resources found
            </h3>
            <p className="text-gray-400">
              Try adjusting your search criteria or filters.
            </p>
          </div>
        )}

        {/* Bookmarked Resources Summary */}
        {bookmarkedResources.size > 0 && (
          <motion.div
            initial={{ opacity: 0, y: 50 }}
            animate={{ opacity: 1, y: 0 }}
            className="mt-12"
          >
            <GlassCard>
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-xl font-semibold text-white flex items-center">
                  <BookmarkCheck className="h-5 w-5 mr-2 text-accent-blue" />
                  Bookmarked Resources ({bookmarkedResources.size})
                </h3>
                <Button variant="outline" size="sm">
                  View All Bookmarks
                </Button>
              </div>
              <p className="text-gray-400">
                You have bookmarked {bookmarkedResources.size} resources. 
                Access them anytime from your personal library to continue your learning journey.
              </p>
            </GlassCard>
          </motion.div>
        )}
      </div>
    </div>
  );
};

export default Resources;