import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Presentation as PresentationChart, Plus, Edit3, Download, Share2, Eye, Palette, Type, Image as ImageIcon, BarChart3, Users, Target, Lightbulb, TrendingUp, DollarSign, Calendar, Award } from 'lucide-react';
import GlassCard from '../components/UI/GlassCard';
import Button from '../components/UI/Button';
import LoadingSpinner from '../components/UI/LoadingSpinner';
import { supabase } from '../lib/supabase';

const PitchDeck = () => {
  const [templates, setTemplates] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedTemplate, setSelectedTemplate] = useState(null);
  const [showEditor, setShowEditor] = useState(false);
  const [currentSlide, setCurrentSlide] = useState(0);

  const slideTypes = [
    { icon: Target, title: 'Problem', description: 'Define the problem you\'re solving' },
    { icon: Lightbulb, title: 'Solution', description: 'Present your unique solution' },
    { icon: TrendingUp, title: 'Market', description: 'Show market size and opportunity' },
    { icon: Award, title: 'Product', description: 'Demonstrate your product' },
    { icon: BarChart3, title: 'Traction', description: 'Highlight your progress' },
    { icon: DollarSign, title: 'Business Model', description: 'Explain how you make money' },
    { icon: Users, title: 'Team', description: 'Introduce your team' },
    { icon: Calendar, title: 'Timeline', description: 'Show your roadmap' },
  ];

  const colorSchemes = [
    { name: 'Ocean', primary: '#0ea5e9', secondary: '#06b6d4', accent: '#8b5cf6' },
    { name: 'Forest', primary: '#10b981', secondary: '#059669', accent: '#f59e0b' },
    { name: 'Sunset', primary: '#f97316', secondary: '#ea580c', accent: '#ec4899' },
    { name: 'Royal', primary: '#8b5cf6', secondary: '#7c3aed', accent: '#06b6d4' },
  ];

  useEffect(() => {
    fetchTemplates();
  }, []);

  const fetchTemplates = async () => {
    try {
      const { data, error } = await supabase.from('pitchDecks').select('*');
      if (!error) {
        setTemplates(data);
      }
    } catch (error) {
      console.error('Error fetching templates:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleTemplateSelect = (template) => {
    setSelectedTemplate(template);
    setShowEditor(true);
  };

  const mockSlideContent = {
    0: {
      title: 'Revolutionary SaaS Platform',
      subtitle: 'Transforming how businesses manage their operations',
      content: 'Our AI-powered platform helps companies streamline their workflows and increase productivity by 40%.'
    },
    1: {
      title: 'The Problem',
      subtitle: 'Businesses struggle with inefficient processes',
      content: '• 73% of companies report workflow inefficiencies\n• Average employee wastes 2.5 hours daily on manual tasks\n• $1.8T lost annually due to poor process management'
    },
    2: {
      title: 'Our Solution',
      subtitle: 'AI-powered workflow automation',
      content: '• Intelligent process automation\n• Real-time analytics and insights\n• Seamless integration with existing tools\n• 40% productivity increase guaranteed'
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen pt-20 flex items-center justify-center">
        <LoadingSpinner size="xl" />
      </div>
    );
  }

  return (
    <div className="min-h-screen pt-20">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        {!showEditor ? (
          <>
            {/* Header */}
            <motion.div
              initial={{ opacity: 0, y: 50 }}
              animate={{ opacity: 1, y: 0 }}
              className="text-center mb-12"
            >
              <h1 className="text-4xl md:text-6xl font-bold mb-6">
                <span className="gradient-text">Pitch Deck</span>
                <br />
                <span className="text-white">Generator</span>
              </h1>
              <p className="text-xl text-gray-300 max-w-3xl mx-auto">
                Create investor-ready pitch decks with AI-powered content generation 
                and professional design templates.
              </p>
            </motion.div>

            {/* Templates Grid */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
              {templates.map((template, index) => (
                <motion.div
                  key={template.id}
                  initial={{ opacity: 0, y: 50 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: index * 0.1 }}
                >
                  <GlassCard className="h-full group cursor-pointer" onClick={() => handleTemplateSelect(template)}>
                    <div className="aspect-video bg-gradient-to-br from-gray-800 to-gray-900 rounded-lg mb-4 overflow-hidden relative">
                      <div className="absolute inset-0 bg-gradient-to-br from-accent-blue/20 to-accent-purple/20"></div>
                      <div className="absolute inset-0 flex items-center justify-center">
                        <PresentationChart className="h-16 w-16 text-white/50" />
                      </div>
                      <div className="absolute top-4 right-4">
                        <span className="px-2 py-1 bg-black/50 text-white text-xs rounded-full">
                          {template.slides} slides
                        </span>
                      </div>
                    </div>
                    
                    <div className="flex items-center justify-between mb-3">
                      <h3 className="text-lg font-semibold text-white group-hover:text-accent-blue transition-colors duration-300">
                        {template.title}
                      </h3>
                      <div className="flex space-x-1">
                        <span className="px-2 py-1 bg-accent-blue/20 text-accent-blue text-xs rounded-full">
                          {template.industry}
                        </span>
                        <span className="px-2 py-1 bg-accent-purple/20 text-accent-purple text-xs rounded-full">
                          {template.stage}
                        </span>
                      </div>
                    </div>
                    
                    <p className="text-gray-400 text-sm mb-4">
                      {template.description}
                    </p>
                    
                    <div className="flex space-x-2">
                      <Button size="sm" className="flex-1" icon={Eye}>
                        Preview
                      </Button>
                      <Button variant="outline" size="sm" className="flex-1">
                        Use Template
                      </Button>
                    </div>
                  </GlassCard>
                </motion.div>
              ))}
            </div>

            {/* Create Custom Deck */}
            <motion.div
              initial={{ opacity: 0, y: 50 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.5 }}
              className="mt-12"
            >
              <GlassCard className="text-center py-12 cursor-pointer group hover:bg-gradient-to-br hover:from-accent-blue/10 hover:to-accent-purple/10 transition-all duration-300">
                <div className="w-16 h-16 mx-auto mb-4 rounded-full bg-gradient-to-r from-accent-blue to-accent-purple flex items-center justify-center group-hover:scale-110 transition-transform duration-300">
                  <Plus className="h-8 w-8 text-white" />
                </div>
                <h3 className="text-xl font-semibold text-white mb-2">
                  Create Custom Pitch Deck
                </h3>
                <p className="text-gray-400 mb-4">
                  Start from scratch with our AI-powered content generator
                </p>
                <Button icon={Plus}>
                  Start Building
                </Button>
              </GlassCard>
            </motion.div>
          </>
        ) : (
          /* Pitch Deck Editor */
          <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
            {/* Slide Navigation */}
            <div className="lg:col-span-1">
              <GlassCard className="sticky top-24">
                <h3 className="text-lg font-semibold text-white mb-4">Slides</h3>
                <div className="space-y-2">
                  {slideTypes.map((slide, index) => {
                    const Icon = slide.icon;
                    return (
                      <motion.div
                        key={index}
                        className={`p-3 rounded-lg cursor-pointer transition-all duration-300 ${
                          currentSlide === index 
                            ? 'bg-gradient-to-r from-accent-blue to-accent-purple' 
                            : 'bg-white/5 hover:bg-white/10'
                        }`}
                        onClick={() => setCurrentSlide(index)}
                        whileHover={{ scale: 1.02 }}
                        whileTap={{ scale: 0.98 }}
                      >
                        <div className="flex items-center space-x-3">
                          <Icon className="h-5 w-5 text-white" />
                          <div>
                            <div className="text-sm font-medium text-white">
                              {slide.title}
                            </div>
                            <div className="text-xs text-gray-400">
                              Slide {index + 1}
                            </div>
                          </div>
                        </div>
                      </motion.div>
                    );
                  })}
                </div>

                <div className="mt-6 pt-6 border-t border-white/10">
                  <Button size="sm" variant="outline" className="w-full mb-2" icon={Plus}>
                    Add Slide
                  </Button>
                  <Button size="sm" variant="ghost" className="w-full" icon={Download}>
                    Export PDF
                  </Button>
                </div>
              </GlassCard>
            </div>

            {/* Main Editor */}
            <div className="lg:col-span-2">
              <GlassCard className="mb-6">
                {/* Slide Preview */}
                <div className="aspect-video bg-gradient-to-br from-gray-900 to-gray-800 rounded-lg p-8 mb-6 relative overflow-hidden">
                  <div className="absolute inset-0 bg-gradient-to-br from-accent-blue/10 to-accent-purple/10"></div>
                  <div className="relative z-10">
                    <motion.div
                      key={currentSlide}
                      initial={{ opacity: 0, x: 50 }}
                      animate={{ opacity: 1, x: 0 }}
                      transition={{ duration: 0.3 }}
                    >
                      <h1 className="text-2xl md:text-4xl font-bold text-white mb-4">
                        {mockSlideContent[currentSlide]?.title || slideTypes[currentSlide]?.title}
                      </h1>
                      <h2 className="text-lg md:text-xl text-accent-blue mb-6">
                        {mockSlideContent[currentSlide]?.subtitle || slideTypes[currentSlide]?.description}
                      </h2>
                      <div className="text-gray-300 whitespace-pre-line">
                        {mockSlideContent[currentSlide]?.content || 'Click to add content...'}
                      </div>
                    </motion.div>
                  </div>
                </div>

                {/* Slide Controls */}
                <div className="flex items-center justify-between">
                  <div className="flex space-x-2">
                    <Button
                      size="sm"
                      variant="outline"
                      disabled={currentSlide === 0}
                      onClick={() => setCurrentSlide(Math.max(0, currentSlide - 1))}
                    >
                      Previous
                    </Button>
                    <Button
                      size="sm"
                      variant="outline"
                      disabled={currentSlide === slideTypes.length - 1}
                      onClick={() => setCurrentSlide(Math.min(slideTypes.length - 1, currentSlide + 1))}
                    >
                      Next
                    </Button>
                  </div>
                  <div className="text-sm text-gray-400">
                    {currentSlide + 1} of {slideTypes.length}
                  </div>
                </div>
              </GlassCard>
            </div>

            {/* Design Panel */}
            <div className="lg:col-span-1">
              <GlassCard className="sticky top-24">
                <h3 className="text-lg font-semibold text-white mb-4 flex items-center">
                  <Palette className="h-5 w-5 mr-2" />
                  Design
                </h3>

                {/* Color Schemes */}
                <div className="mb-6">
                  <h4 className="text-sm font-medium text-gray-300 mb-3">Color Scheme</h4>
                  <div className="grid grid-cols-2 gap-2">
                    {colorSchemes.map((scheme) => (
                      <div
                        key={scheme.name}
                        className="p-3 rounded-lg bg-white/5 hover:bg-white/10 cursor-pointer transition-all duration-300"
                      >
                        <div className="flex space-x-1 mb-2">
                          <div 
                            className="w-4 h-4 rounded-full"
                            style={{ backgroundColor: scheme.primary }}
                          ></div>
                          <div 
                            className="w-4 h-4 rounded-full"
                            style={{ backgroundColor: scheme.secondary }}
                          ></div>
                          <div 
                            className="w-4 h-4 rounded-full"
                            style={{ backgroundColor: scheme.accent }}
                          ></div>
                        </div>
                        <div className="text-xs text-gray-400">{scheme.name}</div>
                      </div>
                    ))}
                  </div>
                </div>

                {/* Typography */}
                <div className="mb-6">
                  <h4 className="text-sm font-medium text-gray-300 mb-3 flex items-center">
                    <Type className="h-4 w-4 mr-2" />
                    Typography
                  </h4>
                  <select className="w-full px-3 py-2 form-input rounded-lg text-sm">
                    <option>Inter (Modern)</option>
                    <option>Roboto (Clean)</option>
                    <option>Poppins (Friendly)</option>
                    <option>Montserrat (Professional)</option>
                  </select>
                </div>

                {/* Actions */}
                <div className="space-y-2">
                  <Button size="sm" className="w-full" icon={Share2}>
                    Share Deck
                  </Button>
                  <Button size="sm" variant="outline" className="w-full" icon={Download}>
                    Download
                  </Button>
                  <Button 
                    size="sm" 
                    variant="ghost" 
                    className="w-full"
                    onClick={() => setShowEditor(false)}
                  >
                    Back to Templates
                  </Button>
                </div>
              </GlassCard>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default PitchDeck;