import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  Search, 
  Filter, 
  DollarSign, 
  Calendar, 
  MapPin, 
  Building, 
  Bookmark,
  BookmarkCheck,
  ExternalLink,
  Clock,
  TrendingUp,
  Award,
  AlertCircle,
  Upload,
  Download,
  RefreshCw,
  X,
  Check,
  FileText,
  Users,
  Globe,
  Zap
} from 'lucide-react';
import GlassCard from '../components/UI/GlassCard';
import Button from '../components/UI/Button';
import LoadingSpinner from '../components/UI/LoadingSpinner';

const Grants = () => {
  const [grants, setGrants] = useState([]);
  const [loading, setLoading] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('All');
  const [selectedRegion, setSelectedRegion] = useState('All');
  const [selectedFundingRange, setSelectedFundingRange] = useState('All');
  const [selectedEligibility, setSelectedEligibility] = useState('All');
  const [selectedDeadline, setSelectedDeadline] = useState('All');
  const [selectedStatus, setSelectedStatus] = useState('All');
  const [bookmarkedGrants, setBookmarkedGrants] = useState(new Set());
  const [sortBy, setSortBy] = useState('deadline');
  const [showImportModal, setShowImportModal] = useState(false);
  const [importFile, setImportFile] = useState(null);
  const [importStatus, setImportStatus] = useState('');

  // Sample grants data with comprehensive examples
  const sampleGrants = [
    {
      id: 1,
      name: "Small Business Innovation Research (SBIR)",
      description: "Federal funding for small businesses to engage in R&D with commercialization potential. Supports development of innovative technologies with strong commercial potential.",
      amount: "$50,000 - $1,500,000",
      deadline: "2025-12-15",
      category: "Business/Technology",
      region: "National",
      eligibility: "Small Business",
      official_url: "https://www.sbir.gov",
      status: "Open",
      requirements: "Must be a for-profit small business with fewer than 500 employees",
      funding_type: "Grant",
      tags: ["technology", "innovation", "r&d", "commercialization", "federal"]
    },
    {
      id: 2,
      name: "National Science Foundation Research Grants",
      description: "Support for fundamental research in science and engineering across all disciplines. Promotes the progress of science and advances national health, prosperity, and welfare.",
      amount: "$100,000 - $2,000,000",
      deadline: "2026-01-30",
      category: "Research/Education",
      region: "National",
      eligibility: "Educational Institution",
      official_url: "https://www.nsf.gov",
      status: "Open",
      requirements: "Must be an accredited educational institution or research organization",
      funding_type: "Grant",
      tags: ["research", "science", "engineering", "education", "nsf"]
    },
    {
      id: 3,
      name: "Community Foundation Arts Grant",
      description: "Local funding for arts and cultural programs that enhance community engagement and cultural diversity. Supports both individual artists and arts organizations.",
      amount: "$5,000 - $25,000",
      deadline: "2026-03-01",
      category: "Arts/Culture",
      region: "Local",
      eligibility: "Non-profit Organization",
      official_url: "https://example-foundation.org",
      status: "Open",
      requirements: "Must serve local community and demonstrate cultural impact",
      funding_type: "Grant",
      tags: ["arts", "culture", "community", "non-profit", "local"]
    },
    {
      id: 4,
      name: "Green Energy Innovation Fund",
      description: "Supporting clean energy solutions and sustainable technology development. Focus on renewable energy, energy efficiency, and environmental sustainability projects.",
      amount: "$75,000 - $500,000",
      deadline: "2025-08-15",
      category: "Clean Energy",
      region: "International",
      eligibility: "Startup",
      official_url: "https://greenenergy-fund.org",
      status: "Open",
      requirements: "Must focus on clean energy or sustainability solutions",
      funding_type: "Grant",
      tags: ["clean-energy", "sustainability", "environment", "renewable", "innovation"]
    },
    {
      id: 5,
      name: "Healthcare Innovation Challenge",
      description: "Funding for breakthrough healthcare technologies and medical device innovations. Supports projects that improve patient outcomes and healthcare delivery.",
      amount: "$100,000 - $750,000",
      deadline: "2025-11-30",
      category: "Healthcare",
      region: "National",
      eligibility: "Research Institution",
      official_url: "https://healthinnovation.gov",
      status: "Open",
      requirements: "Must demonstrate potential for significant healthcare impact",
      funding_type: "Grant",
      tags: ["healthcare", "medical", "innovation", "patient-care", "technology"]
    },
    {
      id: 6,
      name: "Digital Education Transformation Grant",
      description: "Supporting educational institutions in digital transformation initiatives. Focus on improving online learning, educational technology, and student engagement.",
      amount: "$25,000 - $200,000",
      deadline: "2025-09-15",
      category: "Education/Technology",
      region: "State",
      eligibility: "Educational Institution",
      official_url: "https://digitaleducation.org",
      status: "Closing Soon",
      requirements: "Must be an accredited educational institution with digital transformation plan",
      funding_type: "Grant",
      tags: ["education", "digital", "technology", "online-learning", "transformation"]
    },
    {
      id: 7,
      name: "Rural Development Initiative",
      description: "Economic development funding for rural communities and businesses. Supports job creation, infrastructure development, and community revitalization projects.",
      amount: "$50,000 - $300,000",
      deadline: "2026-02-28",
      category: "Economic Development",
      region: "Rural",
      eligibility: "Small Business",
      official_url: "https://ruraldevelopment.gov",
      status: "Open",
      requirements: "Must be located in designated rural area and create local jobs",
      funding_type: "Grant",
      tags: ["rural", "economic-development", "community", "jobs", "infrastructure"]
    },
    {
      id: 8,
      name: "Women Entrepreneurs Fund",
      description: "Supporting women-led startups and businesses across all industries. Provides funding, mentorship, and resources for women entrepreneurs to scale their ventures.",
      amount: "$10,000 - $100,000",
      deadline: "2025-10-31",
      category: "Entrepreneurship",
      region: "National",
      eligibility: "Women-owned Business",
      official_url: "https://womenentrepreneurs.org",
      status: "Open",
      requirements: "Must be majority women-owned business with growth potential",
      funding_type: "Grant",
      tags: ["women", "entrepreneurship", "startup", "business", "diversity"]
    }
  ];

  // Filter options
  const categories = ['All', 'Business/Technology', 'Research/Education', 'Arts/Culture', 'Clean Energy', 'Healthcare', 'Education/Technology', 'Economic Development', 'Entrepreneurship'];
  const regions = ['All', 'Local', 'State', 'National', 'International', 'Rural'];
  const fundingRanges = [
    'All',
    'Under $10,000',
    '$10,000 - $50,000',
    '$50,000 - $100,000',
    '$100,000 - $500,000',
    '$500,000+'
  ];
  const eligibilityTypes = ['All', 'Individual', 'Small Business', 'Non-profit Organization', 'Educational Institution', 'Research Institution', 'Startup', 'Women-owned Business'];
  const deadlineProximity = ['All', 'Due within 30 days', 'Due within 60 days', 'Due within 90 days', 'Due in 3+ months'];
  const statusOptions = ['All', 'Open', 'Closing Soon', 'Closed', 'Recurring'];

  useEffect(() => {
    setGrants(sampleGrants);
    // Load bookmarked grants from localStorage
    const savedBookmarks = localStorage.getItem('bookmarkedGrants');
    if (savedBookmarks) {
      setBookmarkedGrants(new Set(JSON.parse(savedBookmarks)));
    }
  }, []);

  // Save bookmarks to localStorage whenever they change
  useEffect(() => {
    localStorage.setItem('bookmarkedGrants', JSON.stringify([...bookmarkedGrants]));
  }, [bookmarkedGrants]);

  const parseAmount = (amountStr) => {
    const numbers = amountStr.match(/\d+/g);
    if (numbers) {
      return parseInt(numbers[numbers.length - 1].replace(/,/g, ''));
    }
    return 0;
  };

  const filterByFundingRange = (grant, range) => {
    if (range === 'All') return true;
    
    const maxAmount = parseAmount(grant.amount);
    
    switch (range) {
      case 'Under $10,000':
        return maxAmount < 10000;
      case '$10,000 - $50,000':
        return maxAmount >= 10000 && maxAmount <= 50000;
      case '$50,000 - $100,000':
        return maxAmount >= 50000 && maxAmount <= 100000;
      case '$100,000 - $500,000':
        return maxAmount >= 100000 && maxAmount <= 500000;
      case '$500,000+':
        return maxAmount > 500000;
      default:
        return true;
    }
  };

  const filterByDeadline = (grant, proximity) => {
    if (proximity === 'All') return true;
    
    const today = new Date();
    const deadline = new Date(grant.deadline);
    const daysUntil = Math.ceil((deadline - today) / (1000 * 60 * 60 * 24));
    
    switch (proximity) {
      case 'Due within 30 days':
        return daysUntil <= 30 && daysUntil >= 0;
      case 'Due within 60 days':
        return daysUntil <= 60 && daysUntil >= 0;
      case 'Due within 90 days':
        return daysUntil <= 90 && daysUntil >= 0;
      case 'Due in 3+ months':
        return daysUntil > 90;
      default:
        return true;
    }
  };

  const filteredGrants = grants
    .filter(grant => {
      const matchesSearch = grant.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                           grant.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
                           grant.tags.some(tag => tag.toLowerCase().includes(searchTerm.toLowerCase()));
      const matchesCategory = selectedCategory === 'All' || grant.category === selectedCategory;
      const matchesRegion = selectedRegion === 'All' || grant.region === selectedRegion;
      const matchesFunding = filterByFundingRange(grant, selectedFundingRange);
      const matchesEligibility = selectedEligibility === 'All' || grant.eligibility === selectedEligibility;
      const matchesDeadline = filterByDeadline(grant, selectedDeadline);
      const matchesStatus = selectedStatus === 'All' || grant.status === selectedStatus;
      
      return matchesSearch && matchesCategory && matchesRegion && matchesFunding && 
             matchesEligibility && matchesDeadline && matchesStatus;
    })
    .sort((a, b) => {
      if (sortBy === 'deadline') {
        return new Date(a.deadline) - new Date(b.deadline);
      } else if (sortBy === 'amount') {
        return parseAmount(b.amount) - parseAmount(a.amount);
      } else if (sortBy === 'name') {
        return a.name.localeCompare(b.name);
      }
      return 0;
    });

  const toggleBookmark = (grantId) => {
    setBookmarkedGrants(prev => {
      const newSet = new Set(prev);
      if (newSet.has(grantId)) {
        newSet.delete(grantId);
      } else {
        newSet.add(grantId);
      }
      return newSet;
    });
  };

  const getDeadlineStatus = (deadline) => {
    const today = new Date();
    const deadlineDate = new Date(deadline);
    const daysUntil = Math.ceil((deadlineDate - today) / (1000 * 60 * 60 * 24));
    
    if (daysUntil < 0) return { status: 'expired', color: 'text-red-400', text: 'Expired', bgColor: 'bg-red-500/20' };
    if (daysUntil <= 7) return { status: 'urgent', color: 'text-red-400', text: `${daysUntil} days left`, bgColor: 'bg-red-500/20' };
    if (daysUntil <= 30) return { status: 'soon', color: 'text-yellow-400', text: `${daysUntil} days left`, bgColor: 'bg-yellow-500/20' };
    return { status: 'open', color: 'text-green-400', text: `${daysUntil} days left`, bgColor: 'bg-green-500/20' };
  };

  const handleCSVImport = (event) => {
    const file = event.target.files[0];
    if (file && file.type === 'text/csv') {
      setImportFile(file);
      setImportStatus('File selected. Click "Import Data" to process.');
    } else {
      setImportStatus('Please select a valid CSV file.');
    }
  };

  const processCSVImport = () => {
    if (!importFile) return;
    
    setLoading(true);
    setImportStatus('Processing CSV file...');
    
    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const csv = e.target.result;
        const lines = csv.split('\n');
        const headers = lines[0].split(',').map(h => h.trim().replace(/"/g, ''));
        
        const newGrants = [];
        for (let i = 1; i < lines.length; i++) {
          if (lines[i].trim()) {
            const values = lines[i].split(',').map(v => v.trim().replace(/"/g, ''));
            const grant = {
              id: Date.now() + i,
              name: values[headers.indexOf('name')] || values[headers.indexOf('title')] || 'Unnamed Grant',
              description: values[headers.indexOf('description')] || 'No description available',
              amount: values[headers.indexOf('amount')] || 'Amount not specified',
              deadline: values[headers.indexOf('deadline')] || values[headers.indexOf('application_deadline')] || '2025-12-31',
              category: values[headers.indexOf('category')] || 'General',
              region: values[headers.indexOf('region')] || 'Not specified',
              eligibility: values[headers.indexOf('eligibility')] || 'See requirements',
              official_url: values[headers.indexOf('official_url')] || values[headers.indexOf('url')] || values[headers.indexOf('website')] || '#',
              status: values[headers.indexOf('status')] || 'Open',
              requirements: values[headers.indexOf('requirements')] || 'See official website for requirements',
              funding_type: values[headers.indexOf('funding_type')] || 'Grant',
              tags: values[headers.indexOf('tags')] ? values[headers.indexOf('tags')].split(';') : ['imported']
            };
            newGrants.push(grant);
          }
        }
        
        setGrants(prev => [...prev, ...newGrants]);
        setImportStatus(`Successfully imported ${newGrants.length} grants!`);
        setTimeout(() => {
          setShowImportModal(false);
          setImportFile(null);
          setImportStatus('');
        }, 2000);
        
      } catch (error) {
        setImportStatus('Error processing CSV file. Please check the format.');
      } finally {
        setLoading(false);
      }
    };
    
    reader.readAsText(importFile);
  };

  const exportBookmarkedGrants = () => {
    const bookmarked = grants.filter(grant => bookmarkedGrants.has(grant.id));
    const csvContent = [
      'name,description,amount,deadline,category,region,eligibility,official_url,status,requirements,funding_type,tags',
      ...bookmarked.map(grant => 
        `"${grant.name}","${grant.description}","${grant.amount}","${grant.deadline}","${grant.category}","${grant.region}","${grant.eligibility}","${grant.official_url}","${grant.status}","${grant.requirements}","${grant.funding_type}","${grant.tags.join(';')}"`
      )
    ].join('\n');
    
    const blob = new Blob([csvContent], { type: 'text/csv' });
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'bookmarked_grants.csv';
    a.click();
    window.URL.revokeObjectURL(url);
  };

  const clearAllFilters = () => {
    setSearchTerm('');
    setSelectedCategory('All');
    setSelectedRegion('All');
    setSelectedFundingRange('All');
    setSelectedEligibility('All');
    setSelectedDeadline('All');
    setSelectedStatus('All');
  };

  const activeFiltersCount = [
    searchTerm,
    selectedCategory !== 'All' ? selectedCategory : null,
    selectedRegion !== 'All' ? selectedRegion : null,
    selectedFundingRange !== 'All' ? selectedFundingRange : null,
    selectedEligibility !== 'All' ? selectedEligibility : null,
    selectedDeadline !== 'All' ? selectedDeadline : null,
    selectedStatus !== 'All' ? selectedStatus : null
  ].filter(Boolean).length;

  const stats = {
    totalGrants: grants.length,
    openGrants: grants.filter(grant => grant.status === 'Open').length,
    closingSoon: grants.filter(grant => getDeadlineStatus(grant.deadline).status === 'urgent').length,
    bookmarked: bookmarkedGrants.size
  };

  return (
    <div className="min-h-screen pt-20">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          className="text-center mb-12"
        >
          <h1 className="text-4xl md:text-6xl font-bold mb-6">
            <span className="gradient-text">Grant Finder</span>
            <br />
            <span className="text-white">Database</span>
          </h1>
          <p className="text-xl text-gray-300 max-w-3xl mx-auto">
            Discover funding opportunities from your integrated Excel database. 
            Search, filter, and apply to grants that match your needs.
          </p>
        </motion.div>

        {/* Stats Dashboard */}
        <div className="grid grid-cols-2 md:grid-cols-4 gap-6 mb-8">
          <GlassCard className="text-center">
            <div className="text-2xl font-bold gradient-text mb-2">
              {stats.totalGrants}
            </div>
            <div className="text-gray-400 text-sm">Total Grants</div>
          </GlassCard>
          <GlassCard className="text-center">
            <div className="text-2xl font-bold text-green-400 mb-2">
              {stats.openGrants}
            </div>
            <div className="text-gray-400 text-sm">Open Now</div>
          </GlassCard>
          <GlassCard className="text-center">
            <div className="text-2xl font-bold text-yellow-400 mb-2">
              {stats.closingSoon}
            </div>
            <div className="text-gray-400 text-sm">Closing Soon</div>
          </GlassCard>
          <GlassCard className="text-center">
            <div className="text-2xl font-bold text-blue-400 mb-2">
              {stats.bookmarked}
            </div>
            <div className="text-gray-400 text-sm">Bookmarked</div>
          </GlassCard>
        </div>

        {/* Database Management */}
        <GlassCard className="mb-8">
          <div className="flex flex-col md:flex-row items-center justify-between mb-4">
            <h3 className="text-xl font-semibold text-white mb-4 md:mb-0">
              Database Management
            </h3>
            <div className="flex flex-wrap gap-3">
              <Button
                variant="outline"
                size="sm"
                icon={Upload}
                onClick={() => setShowImportModal(true)}
              >
                Import CSV
              </Button>
              <Button
                variant="outline"
                size="sm"
                icon={Download}
                onClick={exportBookmarkedGrants}
                disabled={bookmarkedGrants.size === 0}
              >
                Export Bookmarks
              </Button>
              <Button
                variant="ghost"
                size="sm"
                icon={RefreshCw}
                onClick={() => window.location.reload()}
              >
                Refresh
              </Button>
            </div>
          </div>
          <p className="text-gray-400 text-sm">
            Import your Excel data as CSV to add grants to the database. 
            <a href="#" className="text-accent-blue hover:underline ml-1">
              View integration guide
            </a>
          </p>
        </GlassCard>

        {/* Search and Filters */}
        <GlassCard className="mb-8">
          <div className="space-y-4">
            {/* Search Bar */}
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
              <input
                type="text"
                placeholder="Search grants by name, description, or tags..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-3 form-input rounded-lg"
              />
            </div>

            {/* Filter Grid */}
            <div className="grid grid-cols-1 md:grid-cols-3 lg:grid-cols-4 gap-4">
              <select
                value={selectedCategory}
                onChange={(e) => setSelectedCategory(e.target.value)}
                className="px-4 py-3 form-input rounded-lg"
              >
                <option value="">Category</option>
                {categories.map(category => (
                  <option key={category} value={category}>{category}</option>
                ))}
              </select>

              <select
                value={selectedFundingRange}
                onChange={(e) => setSelectedFundingRange(e.target.value)}
                className="px-4 py-3 form-input rounded-lg"
              >
                <option value="">Funding Range</option>
                {fundingRanges.map(range => (
                  <option key={range} value={range}>{range}</option>
                ))}
              </select>

              <select
                value={selectedEligibility}
                onChange={(e) => setSelectedEligibility(e.target.value)}
                className="px-4 py-3 form-input rounded-lg"
              >
                <option value="">Eligibility</option>
                {eligibilityTypes.map(type => (
                  <option key={type} value={type}>{type}</option>
                ))}
              </select>

              <select
                value={selectedDeadline}
                onChange={(e) => setSelectedDeadline(e.target.value)}
                className="px-4 py-3 form-input rounded-lg"
              >
                <option value="">Deadline</option>
                {deadlineProximity.map(proximity => (
                  <option key={proximity} value={proximity}>{proximity}</option>
                ))}
              </select>

              <select
                value={selectedRegion}
                onChange={(e) => setSelectedRegion(e.target.value)}
                className="px-4 py-3 form-input rounded-lg"
              >
                <option value="">Region</option>
                {regions.map(region => (
                  <option key={region} value={region}>{region}</option>
                ))}
              </select>

              <select
                value={selectedStatus}
                onChange={(e) => setSelectedStatus(e.target.value)}
                className="px-4 py-3 form-input rounded-lg"
              >
                <option value="">Status</option>
                {statusOptions.map(status => (
                  <option key={status} value={status}>{status}</option>
                ))}
              </select>

              <select
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value)}
                className="px-4 py-3 form-input rounded-lg"
              >
                <option value="deadline">Sort by Deadline</option>
                <option value="amount">Sort by Amount</option>
                <option value="name">Sort by Name</option>
              </select>

              <Button
                variant="outline"
                onClick={clearAllFilters}
                disabled={activeFiltersCount === 0}
                className="flex items-center justify-center"
              >
                Clear Filters {activeFiltersCount > 0 && `(${activeFiltersCount})`}
              </Button>
            </div>
          </div>
        </GlassCard>

        {/* Results Summary */}
        <div className="flex items-center justify-between mb-6">
          <div className="text-gray-300">
            Showing {filteredGrants.length} of {grants.length} grants
            {activeFiltersCount > 0 && (
              <span className="ml-2 text-accent-blue">
                ({activeFiltersCount} filter{activeFiltersCount !== 1 ? 's' : ''} active)
              </span>
            )}
          </div>
        </div>

        {/* Grants Grid */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {filteredGrants.map((grant, index) => {
            const deadlineStatus = getDeadlineStatus(grant.deadline);
            const isBookmarked = bookmarkedGrants.has(grant.id);
            const isExpired = deadlineStatus.status === 'expired';
            
            return (
              <motion.div
                key={grant.id}
                initial={{ opacity: 0, y: 50 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: index * 0.1 }}
              >
                <GlassCard className={`h-full relative ${isExpired ? 'opacity-75' : ''}`}>
                  {/* Bookmark Button */}
                  <button
                    onClick={() => toggleBookmark(grant.id)}
                    className="absolute top-4 right-4 p-2 rounded-lg hover:bg-white/10 transition-colors duration-300 z-10"
                  >
                    {isBookmarked ? (
                      <BookmarkCheck className="h-5 w-5 text-accent-blue" />
                    ) : (
                      <Bookmark className="h-5 w-5 text-gray-400 hover:text-white" />
                    )}
                  </button>

                  {/* Grant Header */}
                  <div className="mb-4 pr-12">
                    <h3 className="text-xl font-semibold text-white mb-2 line-clamp-2">
                      {grant.name}
                    </h3>
                    
                    <div className="flex flex-wrap items-center gap-2 mb-3">
                      <span className="px-2 py-1 bg-accent-blue/20 text-accent-blue text-xs rounded-full">
                        {grant.category}
                      </span>
                      <span className="px-2 py-1 bg-accent-purple/20 text-accent-purple text-xs rounded-full">
                        {grant.eligibility}
                      </span>
                      <span className={`px-2 py-1 text-xs rounded-full flex items-center space-x-1 ${deadlineStatus.bgColor} ${deadlineStatus.color}`}>
                        <Clock className="h-3 w-3" />
                        <span>{deadlineStatus.text}</span>
                      </span>
                      <span className={`px-2 py-1 text-xs rounded-full ${
                        grant.status === 'Open' ? 'bg-green-500/20 text-green-400' :
                        grant.status === 'Closing Soon' ? 'bg-yellow-500/20 text-yellow-400' :
                        'bg-red-500/20 text-red-400'
                      }`}>
                        {grant.status}
                      </span>
                    </div>

                    <div className="flex items-center space-x-4 text-sm text-gray-400 mb-3">
                      <div className="flex items-center space-x-1">
                        <DollarSign className="h-4 w-4" />
                        <span className="text-accent-green font-semibold">{grant.amount}</span>
                      </div>
                      <div className="flex items-center space-x-1">
                        <MapPin className="h-4 w-4" />
                        <span>{grant.region}</span>
                      </div>
                      <div className="flex items-center space-x-1">
                        <Calendar className="h-4 w-4" />
                        <span>{new Date(grant.deadline).toLocaleDateString()}</span>
                      </div>
                    </div>
                  </div>

                  {/* Grant Description */}
                  <p className="text-gray-400 text-sm mb-4 line-clamp-3">
                    {grant.description}
                  </p>

                  {/* Requirements */}
                  <div className="mb-4">
                    <h4 className="text-sm font-medium text-white mb-2">Requirements:</h4>
                    <p className="text-gray-400 text-xs line-clamp-2">
                      {grant.requirements}
                    </p>
                  </div>

                  {/* Tags */}
                  <div className="flex flex-wrap gap-2 mb-6">
                    {grant.tags.slice(0, 4).map((tag, tagIndex) => (
                      <span
                        key={tagIndex}
                        className="text-xs px-2 py-1 bg-white/10 text-gray-300 rounded-full"
                      >
                        #{tag}
                      </span>
                    ))}
                    {grant.tags.length > 4 && (
                      <span className="text-xs px-2 py-1 bg-accent-blue/20 text-accent-blue rounded-full">
                        +{grant.tags.length - 4} more
                      </span>
                    )}
                  </div>

                  {/* Actions */}
                  <div className="flex space-x-3">
                    <Button
                      size="sm"
                      className="flex-1"
                      icon={ExternalLink}
                      disabled={isExpired || grant.official_url === '#'}
                      onClick={() => window.open(grant.official_url, '_blank')}
                    >
                      {isExpired ? 'Expired' : 'Apply Now'}
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      className="flex-1"
                      icon={FileText}
                    >
                      Details
                    </Button>
                  </div>

                  {/* Urgency Indicator */}
                  {deadlineStatus.status === 'urgent' && !isExpired && (
                    <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-red-500 to-pink-500 rounded-t-xl"></div>
                  )}
                </GlassCard>
              </motion.div>
            );
          })}
        </div>

        {/* No Results */}
        {filteredGrants.length === 0 && (
          <div className="text-center py-12">
            <Search className="h-16 w-16 text-gray-400 mx-auto mb-4" />
            <h3 className="text-xl font-semibold text-white mb-2">
              No grants found
            </h3>
            <p className="text-gray-400 mb-4">
              Try adjusting your search criteria or filters.
            </p>
            <Button variant="outline" onClick={clearAllFilters}>
              Clear All Filters
            </Button>
          </div>
        )}

        {/* Bookmarked Grants Summary */}
        {bookmarkedGrants.size > 0 && (
          <motion.div
            initial={{ opacity: 0, y: 50 }}
            animate={{ opacity: 1, y: 0 }}
            className="mt-12"
          >
            <GlassCard>
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-xl font-semibold text-white flex items-center">
                  <BookmarkCheck className="h-5 w-5 mr-2 text-accent-blue" />
                  Bookmarked Grants ({bookmarkedGrants.size})
                </h3>
                <div className="flex space-x-3">
                  <Button variant="outline" size="sm" icon={Download} onClick={exportBookmarkedGrants}>
                    Export CSV
                  </Button>
                  <Button variant="outline" size="sm">
                    View All
                  </Button>
                </div>
              </div>
              <p className="text-gray-400">
                You have bookmarked {bookmarkedGrants.size} grants. 
                Keep track of deadlines and application requirements to maximize your funding opportunities.
              </p>
            </GlassCard>
          </motion.div>
        )}
      </div>

      {/* Import Modal */}
      <AnimatePresence>
        {showImportModal && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black/50 backdrop-blur-sm z-50 flex items-center justify-center p-4"
          >
            <motion.div
              initial={{ scale: 0.9, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.9, opacity: 0 }}
              className="bg-dark-800 rounded-xl p-6 max-w-md w-full glass border border-white/10"
            >
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-xl font-semibold text-white">Import CSV Data</h3>
                <button
                  onClick={() => setShowImportModal(false)}
                  className="p-2 rounded-lg hover:bg-white/10 transition-colors duration-300"
                >
                  <X className="h-5 w-5 text-gray-400" />
                </button>
              </div>

              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-2">
                    Select CSV File
                  </label>
                  <input
                    type="file"
                    accept=".csv"
                    onChange={handleCSVImport}
                    className="w-full px-3 py-2 form-input rounded-lg text-sm"
                  />
                </div>

                {importStatus && (
                  <div className={`p-3 rounded-lg text-sm ${
                    importStatus.includes('Error') ? 'bg-red-500/20 text-red-400' :
                    importStatus.includes('Successfully') ? 'bg-green-500/20 text-green-400' :
                    'bg-blue-500/20 text-blue-400'
                  }`}>
                    {importStatus}
                  </div>
                )}

                <div className="text-xs text-gray-400">
                  <p className="mb-2">Required CSV columns:</p>
                  <ul className="list-disc list-inside space-y-1">
                    <li>name (or title)</li>
                    <li>description</li>
                    <li>amount</li>
                    <li>deadline</li>
                    <li>category</li>
                    <li>official_url (or url/website)</li>
                  </ul>
                </div>

                <div className="flex space-x-3">
                  <Button
                    onClick={processCSVImport}
                    disabled={!importFile || loading}
                    className="flex-1"
                    icon={loading ? RefreshCw : Upload}
                  >
                    {loading ? 'Processing...' : 'Import Data'}
                  </Button>
                  <Button
                    variant="outline"
                    onClick={() => setShowImportModal(false)}
                    className="flex-1"
                  >
                    Cancel
                  </Button>
                </div>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default Grants;