import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  Brain, 
  ArrowRight, 
  ArrowLeft, 
  CheckCircle, 
  BarChart3,
  Target,
  Users,
  Lightbulb,
  TrendingUp,
  Award
} from 'lucide-react';
import GlassCard from '../components/UI/GlassCard';
import Button from '../components/UI/Button';

const EQSurvey = () => {
  const [currentStep, setCurrentStep] = useState(0);
  const [answers, setAnswers] = useState({});
  const [showResults, setShowResults] = useState(false);

  const surveySteps = [
    {
      title: 'Self-Awareness',
      description: 'Understanding your emotions and their impact',
      questions: [
        {
          id: 'sa1',
          question: 'How well do you understand your emotional triggers?',
          type: 'scale',
          scale: { min: 1, max: 5, labels: ['Not at all', 'Extremely well'] }
        },
        {
          id: 'sa2',
          question: 'When facing setbacks, how do you typically respond?',
          type: 'multiple',
          options: [
            'I analyze what went wrong and learn from it',
            'I feel frustrated but bounce back quickly',
            'I need time to process before moving forward',
            'I tend to blame external factors'
          ]
        },
        {
          id: 'sa3',
          question: 'Rate your ability to recognize your strengths and weaknesses',
          type: 'scale',
          scale: { min: 1, max: 5, labels: ['Poor', 'Excellent'] }
        }
      ]
    },
    {
      title: 'Self-Regulation',
      description: 'Managing your emotions and impulses effectively',
      questions: [
        {
          id: 'sr1',
          question: 'How well do you handle stress and pressure?',
          type: 'scale',
          scale: { min: 1, max: 5, labels: ['Poorly', 'Very well'] }
        },
        {
          id: 'sr2',
          question: 'When making important decisions, you typically:',
          type: 'multiple',
          options: [
            'Take time to consider all options carefully',
            'Trust your gut instinct immediately',
            'Seek advice from others first',
            'Analyze data and metrics thoroughly'
          ]
        },
        {
          id: 'sr3',
          question: 'Rate your ability to stay calm during conflicts',
          type: 'scale',
          scale: { min: 1, max: 5, labels: ['Very difficult', 'Very easy'] }
        }
      ]
    },
    {
      title: 'Motivation',
      description: 'Your drive to achieve and persist through challenges',
      questions: [
        {
          id: 'm1',
          question: 'What primarily motivates you as an entrepreneur?',
          type: 'multiple',
          options: [
            'Making a positive impact on the world',
            'Financial success and independence',
            'Building something innovative and unique',
            'Leading and inspiring a team'
          ]
        },
        {
          id: 'm2',
          question: 'Rate your persistence when facing repeated failures',
          type: 'scale',
          scale: { min: 1, max: 5, labels: ['Give up easily', 'Never give up'] }
        },
        {
          id: 'm3',
          question: 'How do you maintain motivation during difficult periods?',
          type: 'multiple',
          options: [
            'Focus on the bigger vision and purpose',
            'Break down goals into smaller milestones',
            'Seek support from mentors and peers',
            'Remind myself of past successes'
          ]
        }
      ]
    },
    {
      title: 'Empathy',
      description: 'Understanding and relating to others\' emotions',
      questions: [
        {
          id: 'e1',
          question: 'Rate your ability to understand your team members\' perspectives',
          type: 'scale',
          scale: { min: 1, max: 5, labels: ['Poor', 'Excellent'] }
        },
        {
          id: 'e2',
          question: 'When a team member is struggling, you typically:',
          type: 'multiple',
          options: [
            'Have a one-on-one conversation to understand their challenges',
            'Provide additional resources and support',
            'Give them space to work through it themselves',
            'Reassign their tasks to ensure project success'
          ]
        },
        {
          id: 'e3',
          question: 'How well do you read non-verbal cues in meetings?',
          type: 'scale',
          scale: { min: 1, max: 5, labels: ['Not well', 'Very well'] }
        }
      ]
    },
    {
      title: 'Social Skills',
      description: 'Building relationships and influencing others',
      questions: [
        {
          id: 'ss1',
          question: 'Rate your comfort level when networking with new people',
          type: 'scale',
          scale: { min: 1, max: 5, labels: ['Very uncomfortable', 'Very comfortable'] }
        },
        {
          id: 'ss2',
          question: 'Your preferred communication style is:',
          type: 'multiple',
          options: [
            'Direct and straightforward',
            'Collaborative and inclusive',
            'Inspiring and visionary',
            'Analytical and data-driven'
          ]
        },
        {
          id: 'ss3',
          question: 'How effectively do you resolve conflicts within your team?',
          type: 'scale',
          scale: { min: 1, max: 5, labels: ['Not effectively', 'Very effectively'] }
        }
      ]
    }
  ];

  const totalQuestions = surveySteps.reduce((total, step) => total + step.questions.length, 0);
  const answeredQuestions = Object.keys(answers).length;
  const progress = (answeredQuestions / totalQuestions) * 100;

  const handleAnswer = (questionId, answer) => {
    setAnswers(prev => ({ ...prev, [questionId]: answer }));
  };

  const nextStep = () => {
    if (currentStep < surveySteps.length - 1) {
      setCurrentStep(currentStep + 1);
    } else {
      setShowResults(true);
    }
  };

  const prevStep = () => {
    if (currentStep > 0) {
      setCurrentStep(currentStep - 1);
    }
  };

  const calculateResults = () => {
    const categories = {
      'Self-Awareness': ['sa1', 'sa2', 'sa3'],
      'Self-Regulation': ['sr1', 'sr2', 'sr3'],
      'Motivation': ['m1', 'm2', 'm3'],
      'Empathy': ['e1', 'e2', 'e3'],
      'Social Skills': ['ss1', 'ss2', 'ss3']
    };

    const results = {};
    Object.entries(categories).forEach(([category, questionIds]) => {
      const scores = questionIds.map(id => {
        const answer = answers[id];
        if (typeof answer === 'number') return answer;
        return 3; // Default score for multiple choice
      });
      results[category] = Math.round((scores.reduce((a, b) => a + b, 0) / scores.length) * 20);
    });

    return results;
  };

  const getRecommendations = (results) => {
    const recommendations = [];
    
    Object.entries(results).forEach(([category, score]) => {
      if (score < 60) {
        switch (category) {
          case 'Self-Awareness':
            recommendations.push({
              category,
              title: 'Develop Self-Awareness',
              description: 'Practice mindfulness and regular self-reflection',
              actions: ['Keep a daily emotion journal', 'Seek feedback from trusted advisors', 'Practice meditation']
            });
            break;
          case 'Self-Regulation':
            recommendations.push({
              category,
              title: 'Improve Self-Regulation',
              description: 'Build better emotional control and decision-making skills',
              actions: ['Practice stress management techniques', 'Implement decision-making frameworks', 'Develop healthy coping mechanisms']
            });
            break;
          case 'Motivation':
            recommendations.push({
              category,
              title: 'Strengthen Motivation',
              description: 'Clarify your purpose and build resilience',
              actions: ['Define your core values and mission', 'Set clear, achievable milestones', 'Build a support network']
            });
            break;
          case 'Empathy':
            recommendations.push({
              category,
              title: 'Enhance Empathy',
              description: 'Improve your ability to understand others',
              actions: ['Practice active listening', 'Seek diverse perspectives', 'Conduct regular team check-ins']
            });
            break;
          case 'Social Skills':
            recommendations.push({
              category,
              title: 'Build Social Skills',
              description: 'Develop stronger communication and leadership abilities',
              actions: ['Join networking groups', 'Practice public speaking', 'Learn conflict resolution techniques']
            });
            break;
        }
      }
    });

    return recommendations;
  };

  if (showResults) {
    const results = calculateResults();
    const recommendations = getRecommendations(results);
    const overallScore = Math.round(Object.values(results).reduce((a, b) => a + b, 0) / 5);

    return (
      <div className="min-h-screen pt-20">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
          <motion.div
            initial={{ opacity: 0, y: 50 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-center mb-12"
          >
            <div className="w-16 h-16 mx-auto mb-4 rounded-full bg-gradient-to-r from-accent-green to-accent-blue flex items-center justify-center">
              <CheckCircle className="h-8 w-8 text-white" />
            </div>
            <h1 className="text-4xl md:text-5xl font-bold mb-4">
              <span className="text-white">Your EQ</span>
              <br />
              <span className="gradient-text">Assessment Results</span>
            </h1>
            <p className="text-xl text-gray-300">
              Here's your comprehensive emotional intelligence profile
            </p>
          </motion.div>

          {/* Overall Score */}
          <GlassCard className="text-center mb-8">
            <h2 className="text-2xl font-bold text-white mb-4">Overall EQ Score</h2>
            <div className="text-6xl font-bold gradient-text mb-4">{overallScore}</div>
            <div className="text-gray-400">
              {overallScore >= 80 ? 'Excellent' : 
               overallScore >= 60 ? 'Good' : 
               overallScore >= 40 ? 'Average' : 'Needs Improvement'}
            </div>
          </GlassCard>

          {/* Category Breakdown */}
          <GlassCard className="mb-8">
            <h3 className="text-xl font-semibold text-white mb-6 flex items-center">
              <BarChart3 className="h-5 w-5 mr-2" />
              Category Breakdown
            </h3>
            <div className="space-y-4">
              {Object.entries(results).map(([category, score]) => (
                <div key={category}>
                  <div className="flex justify-between items-center mb-2">
                    <span className="text-gray-300">{category}</span>
                    <span className="text-white font-semibold">{score}%</span>
                  </div>
                  <div className="w-full bg-gray-700 rounded-full h-3">
                    <motion.div
                      className={`h-3 rounded-full ${
                        score >= 80 ? 'bg-gradient-to-r from-green-500 to-emerald-500' :
                        score >= 60 ? 'bg-gradient-to-r from-blue-500 to-cyan-500' :
                        score >= 40 ? 'bg-gradient-to-r from-yellow-500 to-orange-500' :
                        'bg-gradient-to-r from-red-500 to-pink-500'
                      }`}
                      initial={{ width: 0 }}
                      animate={{ width: `${score}%` }}
                      transition={{ duration: 1, delay: 0.5 }}
                    />
                  </div>
                </div>
              ))}
            </div>
          </GlassCard>

          {/* Recommendations */}
          {recommendations.length > 0 && (
            <GlassCard className="mb-8">
              <h3 className="text-xl font-semibold text-white mb-6 flex items-center">
                <Target className="h-5 w-5 mr-2" />
                Personalized Recommendations
              </h3>
              <div className="space-y-6">
                {recommendations.map((rec, index) => (
                  <motion.div
                    key={rec.category}
                    initial={{ opacity: 0, x: -50 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ delay: index * 0.2 }}
                    className="border-l-4 border-accent-blue pl-4"
                  >
                    <h4 className="text-lg font-semibold text-white mb-2">{rec.title}</h4>
                    <p className="text-gray-400 mb-3">{rec.description}</p>
                    <ul className="space-y-1">
                      {rec.actions.map((action, actionIndex) => (
                        <li key={actionIndex} className="text-sm text-gray-300 flex items-center">
                          <CheckCircle className="h-4 w-4 text-accent-green mr-2" />
                          {action}
                        </li>
                      ))}
                    </ul>
                  </motion.div>
                ))}
              </div>
            </GlassCard>
          )}

          {/* Actions */}
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <Button icon={Award}>
              Save Results
            </Button>
            <Button variant="outline" onClick={() => {
              setShowResults(false);
              setCurrentStep(0);
              setAnswers({});
            }}>
              Retake Assessment
            </Button>
          </div>
        </div>
      </div>
    );
  }

  const currentStepData = surveySteps[currentStep];
  const isStepComplete = currentStepData.questions.every(q => answers[q.id] !== undefined);

  return (
    <div className="min-h-screen pt-20">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          className="text-center mb-12"
        >
          <h1 className="text-4xl md:text-6xl font-bold mb-6">
            <span className="gradient-text">Founder EQ</span>
            <br />
            <span className="text-white">Assessment</span>
          </h1>
          <p className="text-xl text-gray-300 max-w-3xl mx-auto">
            Evaluate your emotional intelligence and discover insights to become 
            a more effective leader and entrepreneur.
          </p>
        </motion.div>

        {/* Progress Bar */}
        <GlassCard className="mb-8">
          <div className="flex justify-between items-center mb-4">
            <span className="text-gray-300">Progress</span>
            <span className="text-white font-semibold">{Math.round(progress)}%</span>
          </div>
          <div className="w-full bg-gray-700 rounded-full h-3">
            <motion.div
              className="progress-bar h-3 rounded-full"
              initial={{ width: 0 }}
              animate={{ width: `${progress}%` }}
              transition={{ duration: 0.5 }}
            />
          </div>
          <div className="flex justify-between text-sm text-gray-400 mt-2">
            <span>Step {currentStep + 1} of {surveySteps.length}</span>
            <span>{answeredQuestions} of {totalQuestions} questions</span>
          </div>
        </GlassCard>

        {/* Current Step */}
        <AnimatePresence mode="wait">
          <motion.div
            key={currentStep}
            initial={{ opacity: 0, x: 50 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: -50 }}
            transition={{ duration: 0.3 }}
          >
            <GlassCard className="mb-8">
              <div className="text-center mb-8">
                <div className="w-12 h-12 mx-auto mb-4 rounded-full bg-gradient-to-r from-accent-blue to-accent-purple flex items-center justify-center">
                  <Brain className="h-6 w-6 text-white" />
                </div>
                <h2 className="text-2xl font-bold text-white mb-2">
                  {currentStepData.title}
                </h2>
                <p className="text-gray-400">
                  {currentStepData.description}
                </p>
              </div>

              <div className="space-y-8">
                {currentStepData.questions.map((question, qIndex) => (
                  <div key={question.id} className="border-b border-white/10 pb-6 last:border-b-0">
                    <h3 className="text-lg font-medium text-white mb-4">
                      {qIndex + 1}. {question.question}
                    </h3>

                    {question.type === 'scale' && (
                      <div className="space-y-4">
                        <div className="flex justify-between text-sm text-gray-400">
                          <span>{question.scale.labels[0]}</span>
                          <span>{question.scale.labels[1]}</span>
                        </div>
                        <div className="flex space-x-2">
                          {[...Array(question.scale.max)].map((_, i) => (
                            <button
                              key={i}
                              onClick={() => handleAnswer(question.id, i + 1)}
                              className={`flex-1 py-3 rounded-lg text-center transition-all duration-300 ${
                                answers[question.id] === i + 1
                                  ? 'bg-gradient-to-r from-accent-blue to-accent-purple text-white'
                                  : 'bg-white/5 text-gray-400 hover:bg-white/10 hover:text-white'
                              }`}
                            >
                              {i + 1}
                            </button>
                          ))}
                        </div>
                      </div>
                    )}

                    {question.type === 'multiple' && (
                      <div className="space-y-3">
                        {question.options.map((option, oIndex) => (
                          <button
                            key={oIndex}
                            onClick={() => handleAnswer(question.id, option)}
                            className={`w-full p-4 text-left rounded-lg transition-all duration-300 ${
                              answers[question.id] === option
                                ? 'bg-gradient-to-r from-accent-blue to-accent-purple text-white'
                                : 'bg-white/5 text-gray-300 hover:bg-white/10 hover:text-white'
                            }`}
                          >
                            {option}
                          </button>
                        ))}
                      </div>
                    )}
                  </div>
                ))}
              </div>
            </GlassCard>
          </motion.div>
        </AnimatePresence>

        {/* Navigation */}
        <div className="flex justify-between">
          <Button
            variant="outline"
            onClick={prevStep}
            disabled={currentStep === 0}
            icon={ArrowLeft}
          >
            Previous
          </Button>
          <Button
            onClick={nextStep}
            disabled={!isStepComplete}
            icon={currentStep === surveySteps.length - 1 ? CheckCircle : ArrowRight}
            iconPosition="right"
          >
            {currentStep === surveySteps.length - 1 ? 'View Results' : 'Next'}
          </Button>
        </div>
      </div>
    </div>
  );
};

export default EQSurvey;