const mockSupabase = {
  from: (table) => ({
    select: (columns = '*') => ({
      eq: (column, value) => ({
        data: getMockData(table, { [column]: value }),
        error: null
      }),
      data: getMockData(table),
      error: null
    }),
    insert: (data) => ({
      data: { ...data, id: Date.now() },
      error: null
    }),
    update: (data) => ({
      eq: (column, value) => ({
        data: { ...data, [column]: value },
        error: null
      })
    }),
    delete: () => ({
      eq: (column, value) => ({
        data: null,
        error: null
      })
    })
  })
};

