import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  ClipboardList, 
  Plus, 
  Edit3, 
  Eye, 
  Download, 
  Share2,
  Type,
  CheckSquare,
  Circle,
  BarChart3,
  Star,
  Calendar,
  Trash2,
  Copy,
  Settings
} from 'lucide-react';
import GlassCard from '../components/UI/GlassCard';
import Button from '../components/UI/Button';
import { supabase } from '../lib/supabase';

const SurveyGenerator = () => {
  const [surveys, setSurveys] = useState([]);
  const [showBuilder, setShowBuilder] = useState(false);
  const [currentSurvey, setCurrentSurvey] = useState({
    title: '',
    description: '',
    questions: []
  });
  const [showPreview, setShowPreview] = useState(false);

  const questionTypes = [
    { 
      type: 'text', 
      icon: Type, 
      label: 'Text Input', 
      description: 'Single line text response' 
    },
    { 
      type: 'textarea', 
      icon: Edit3, 
      label: 'Long Text', 
      description: 'Multi-line text response' 
    },
    { 
      type: 'multiple-choice', 
      icon: Circle, 
      label: 'Multiple Choice', 
      description: 'Select one option' 
    },
    { 
      type: 'checkbox', 
      icon: CheckSquare, 
      label: 'Checkboxes', 
      description: 'Select multiple options' 
    },
    { 
      type: 'rating', 
      icon: Star, 
      label: 'Rating Scale', 
      description: '1-5 or 1-10 rating' 
    },
    { 
      type: 'scale', 
      icon: BarChart3, 
      label: 'Linear Scale', 
      description: 'Numbered scale response' 
    },
    { 
      type: 'date', 
      icon: Calendar, 
      label: 'Date', 
      description: 'Date picker input' 
    }
  ];

  const surveyTemplates = [
    {
      id: 1,
      title: 'Customer Satisfaction Survey',
      description: 'Measure customer satisfaction and gather feedback',
      industry: 'All',
      questions: 8,
      template: 'customer-satisfaction'
    },
    {
      id: 2,
      title: 'Product Feedback Survey',
      description: 'Collect feedback on product features and usability',
      industry: 'SaaS',
      questions: 12,
      template: 'product-feedback'
    },
    {
      id: 3,
      title: 'Market Research Survey',
      description: 'Understand market needs and preferences',
      industry: 'All',
      questions: 15,
      template: 'market-research'
    },
    {
      id: 4,
      title: 'Employee Engagement Survey',
      description: 'Assess team satisfaction and engagement',
      industry: 'HR',
      questions: 20,
      template: 'employee-engagement'
    }
  ];

  const addQuestion = (type) => {
    const newQuestion = {
      id: Date.now(),
      type,
      question: '',
      required: false,
      options: type === 'multiple-choice' || type === 'checkbox' ? ['Option 1', 'Option 2'] : [],
      scale: type === 'rating' || type === 'scale' ? { min: 1, max: 5 } : null
    };

    setCurrentSurvey(prev => ({
      ...prev,
      questions: [...prev.questions, newQuestion]
    }));
  };

  const updateQuestion = (questionId, updates) => {
    setCurrentSurvey(prev => ({
      ...prev,
      questions: prev.questions.map(q => 
        q.id === questionId ? { ...q, ...updates } : q
      )
    }));
  };

  const deleteQuestion = (questionId) => {
    setCurrentSurvey(prev => ({
      ...prev,
      questions: prev.questions.filter(q => q.id !== questionId)
    }));
  };

  const duplicateQuestion = (questionId) => {
    const questionToDuplicate = currentSurvey.questions.find(q => q.id === questionId);
    if (questionToDuplicate) {
      const duplicatedQuestion = {
        ...questionToDuplicate,
        id: Date.now(),
        question: questionToDuplicate.question + ' (Copy)'
      };
      setCurrentSurvey(prev => ({
        ...prev,
        questions: [...prev.questions, duplicatedQuestion]
      }));
    }
  };

  const addOption = (questionId) => {
    updateQuestion(questionId, {
      options: [...currentSurvey.questions.find(q => q.id === questionId).options, `Option ${currentSurvey.questions.find(q => q.id === questionId).options.length + 1}`]
    });
  };

  const updateOption = (questionId, optionIndex, value) => {
    const question = currentSurvey.questions.find(q => q.id === questionId);
    const newOptions = [...question.options];
    newOptions[optionIndex] = value;
    updateQuestion(questionId, { options: newOptions });
  };

  const removeOption = (questionId, optionIndex) => {
    const question = currentSurvey.questions.find(q => q.id === questionId);
    const newOptions = question.options.filter((_, index) => index !== optionIndex);
    updateQuestion(questionId, { options: newOptions });
  };

  const saveSurvey = () => {
    const newSurvey = {
      ...currentSurvey,
      id: Date.now(),
      createdAt: new Date().toISOString(),
      responses: 0
    };
    setSurveys(prev => [...prev, newSurvey]);
    setShowBuilder(false);
    setCurrentSurvey({ title: '', description: '', questions: [] });
  };

  const startNewSurvey = () => {
    setCurrentSurvey({ title: '', description: '', questions: [] });
    setShowBuilder(true);
  };

  const useTemplate = (template) => {
    // Mock template data
    const templateData = {
      title: template.title,
      description: template.description,
      questions: [
        {
          id: 1,
          type: 'rating',
          question: 'How satisfied are you with our service?',
          required: true,
          scale: { min: 1, max: 5 }
        },
        {
          id: 2,
          type: 'multiple-choice',
          question: 'How did you hear about us?',
          required: false,
          options: ['Social Media', 'Search Engine', 'Word of Mouth', 'Advertisement']
        },
        {
          id: 3,
          type: 'textarea',
          question: 'What can we improve?',
          required: false
        }
      ]
    };
    setCurrentSurvey(templateData);
    setShowBuilder(true);
  };

  const renderQuestionEditor = (question) => {
    const QuestionIcon = questionTypes.find(t => t.type === question.type)?.icon || Type;

    return (
      <GlassCard key={question.id} className="mb-4">
        <div className="flex items-start justify-between mb-4">
          <div className="flex items-center space-x-3">
            <div className="p-2 rounded-lg bg-gradient-to-r from-accent-blue to-accent-purple">
              <QuestionIcon className="h-5 w-5 text-white" />
            </div>
            <div>
              <div className="text-sm font-medium text-white">
                {questionTypes.find(t => t.type === question.type)?.label}
              </div>
              <div className="text-xs text-gray-400">
                {question.required ? 'Required' : 'Optional'}
              </div>
            </div>
          </div>
          <div className="flex space-x-2">
            <button
              onClick={() => duplicateQuestion(question.id)}
              className="p-2 rounded-lg bg-white/5 hover:bg-white/10 transition-colors duration-300"
            >
              <Copy className="h-4 w-4 text-gray-400" />
            </button>
            <button
              onClick={() => deleteQuestion(question.id)}
              className="p-2 rounded-lg bg-white/5 hover:bg-red-500/20 transition-colors duration-300"
            >
              <Trash2 className="h-4 w-4 text-gray-400 hover:text-red-400" />
            </button>
          </div>
        </div>

        {/* Question Text */}
        <div className="mb-4">
          <input
            type="text"
            placeholder="Enter your question..."
            value={question.question}
            onChange={(e) => updateQuestion(question.id, { question: e.target.value })}
            className="w-full px-4 py-3 form-input rounded-lg text-white"
          />
        </div>

        {/* Question Options */}
        {(question.type === 'multiple-choice' || question.type === 'checkbox') && (
          <div className="mb-4">
            <div className="space-y-2">
              {question.options.map((option, index) => (
                <div key={index} className="flex items-center space-x-2">
                  <input
                    type="text"
                    value={option}
                    onChange={(e) => updateOption(question.id, index, e.target.value)}
                    className="flex-1 px-3 py-2 form-input rounded-lg text-sm"
                  />
                  {question.options.length > 2 && (
                    <button
                      onClick={() => removeOption(question.id, index)}
                      className="p-2 rounded-lg bg-red-500/20 hover:bg-red-500/30 transition-colors duration-300"
                    >
                      <Trash2 className="h-4 w-4 text-red-400" />
                    </button>
                  )}
                </div>
              ))}
            </div>
            <Button
              variant="ghost"
              size="sm"
              onClick={() => addOption(question.id)}
              className="mt-2"
              icon={Plus}
            >
              Add Option
            </Button>
          </div>
        )}

        {/* Scale Settings */}
        {(question.type === 'rating' || question.type === 'scale') && (
          <div className="mb-4 grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm text-gray-400 mb-2">Min Value</label>
              <input
                type="number"
                value={question.scale?.min || 1}
                onChange={(e) => updateQuestion(question.id, { 
                  scale: { ...question.scale, min: parseInt(e.target.value) }
                })}
                className="w-full px-3 py-2 form-input rounded-lg text-sm"
              />
            </div>
            <div>
              <label className="block text-sm text-gray-400 mb-2">Max Value</label>
              <input
                type="number"
                value={question.scale?.max || 5}
                onChange={(e) => updateQuestion(question.id, { 
                  scale: { ...question.scale, max: parseInt(e.target.value) }
                })}
                className="w-full px-3 py-2 form-input rounded-lg text-sm"
              />
            </div>
          </div>
        )}

        {/* Question Settings */}
        <div className="flex items-center space-x-4">
          <label className="flex items-center space-x-2 cursor-pointer">
            <input
              type="checkbox"
              checked={question.required}
              onChange={(e) => updateQuestion(question.id, { required: e.target.checked })}
              className="rounded border-gray-600 bg-gray-700 text-accent-blue focus:ring-accent-blue"
            />
            <span className="text-sm text-gray-400">Required</span>
          </label>
        </div>
      </GlassCard>
    );
  };

  if (showPreview) {
    return (
      <div className="min-h-screen pt-20">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
          <div className="flex items-center justify-between mb-8">
            <h1 className="text-3xl font-bold text-white">Survey Preview</h1>
            <Button variant="outline" onClick={() => setShowPreview(false)}>
              Back to Editor
            </Button>
          </div>

          <GlassCard>
            <div className="mb-8">
              <h2 className="text-2xl font-bold text-white mb-4">{currentSurvey.title}</h2>
              <p className="text-gray-400">{currentSurvey.description}</p>
            </div>

            <div className="space-y-6">
              {currentSurvey.questions.map((question, index) => (
                <div key={question.id} className="border-b border-white/10 pb-6 last:border-b-0">
                  <h3 className="text-lg font-medium text-white mb-4">
                    {index + 1}. {question.question}
                    {question.required && <span className="text-red-400 ml-1">*</span>}
                  </h3>

                  {question.type === 'text' && (
                    <input
                      type="text"
                      placeholder="Your answer..."
                      className="w-full px-4 py-3 form-input rounded-lg"
                      disabled
                    />
                  )}

                  {question.type === 'textarea' && (
                    <textarea
                      placeholder="Your answer..."
                      rows={4}
                      className="w-full px-4 py-3 form-input rounded-lg resize-none"
                      disabled
                    />
                  )}

                  {question.type === 'multiple-choice' && (
                    <div className="space-y-2">
                      {question.options.map((option, optionIndex) => (
                        <label key={optionIndex} className="flex items-center space-x-3 cursor-pointer">
                          <input type="radio" name={`question-${question.id}`} disabled />
                          <span className="text-gray-300">{option}</span>
                        </label>
                      ))}
                    </div>
                  )}

                  {question.type === 'checkbox' && (
                    <div className="space-y-2">
                      {question.options.map((option, optionIndex) => (
                        <label key={optionIndex} className="flex items-center space-x-3 cursor-pointer">
                          <input type="checkbox" disabled />
                          <span className="text-gray-300">{option}</span>
                        </label>
                      ))}
                    </div>
                  )}

                  {(question.type === 'rating' || question.type === 'scale') && (
                    <div className="flex space-x-2">
                      {[...Array(question.scale.max - question.scale.min + 1)].map((_, i) => (
                        <button
                          key={i}
                          className="px-4 py-2 bg-white/5 hover:bg-accent-blue/20 rounded-lg transition-colors duration-300"
                          disabled
                        >
                          {question.scale.min + i}
                        </button>
                      ))}
                    </div>
                  )}

                  {question.type === 'date' && (
                    <input
                      type="date"
                      className="px-4 py-3 form-input rounded-lg"
                      disabled
                    />
                  )}
                </div>
              ))}
            </div>

            <div className="mt-8 pt-6 border-t border-white/10">
              <Button className="w-full" disabled>
                Submit Survey
              </Button>
            </div>
          </GlassCard>
        </div>
      </div>
    );
  }

  if (showBuilder) {
    return (
      <div className="min-h-screen pt-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
          <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
            {/* Question Types Sidebar */}
            <div className="lg:col-span-1">
              <GlassCard className="sticky top-24">
                <h3 className="text-lg font-semibold text-white mb-4">Question Types</h3>
                <div className="space-y-2">
                  {questionTypes.map((type) => {
                    const Icon = type.icon;
                    return (
                      <button
                        key={type.type}
                        onClick={() => addQuestion(type.type)}
                        className="w-full p-3 text-left rounded-lg bg-white/5 hover:bg-white/10 transition-all duration-300 group"
                      >
                        <div className="flex items-center space-x-3">
                          <Icon className="h-5 w-5 text-accent-blue group-hover:text-accent-green transition-colors duration-300" />
                          <div>
                            <div className="text-sm font-medium text-white">
                              {type.label}
                            </div>
                            <div className="text-xs text-gray-400">
                              {type.description}
                            </div>
                          </div>
                        </div>
                      </button>
                    );
                  })}
                </div>

                <div className="mt-6 pt-6 border-t border-white/10 space-y-2">
                  <Button
                    size="sm"
                    variant="outline"
                    className="w-full"
                    icon={Eye}
                    onClick={() => setShowPreview(true)}
                  >
                    Preview
                  </Button>
                  <Button
                    size="sm"
                    className="w-full"
                    onClick={saveSurvey}
                    disabled={!currentSurvey.title || currentSurvey.questions.length === 0}
                  >
                    Save Survey
                  </Button>
                  <Button
                    size="sm"
                    variant="ghost"
                    className="w-full"
                    onClick={() => setShowBuilder(false)}
                  >
                    Cancel
                  </Button>
                </div>
              </GlassCard>
            </div>

            {/* Survey Builder */}
            <div className="lg:col-span-3">
              {/* Survey Header */}
              <GlassCard className="mb-6">
                <div className="space-y-4">
                  <input
                    type="text"
                    placeholder="Survey Title"
                    value={currentSurvey.title}
                    onChange={(e) => setCurrentSurvey(prev => ({ ...prev, title: e.target.value }))}
                    className="w-full px-4 py-3 form-input rounded-lg text-xl font-semibold"
                  />
                  <textarea
                    placeholder="Survey Description (optional)"
                    value={currentSurvey.description}
                    onChange={(e) => setCurrentSurvey(prev => ({ ...prev, description: e.target.value }))}
                    rows={3}
                    className="w-full px-4 py-3 form-input rounded-lg resize-none"
                  />
                </div>
              </GlassCard>

              {/* Questions */}
              <div className="space-y-4">
                {currentSurvey.questions.map(renderQuestionEditor)}
              </div>

              {currentSurvey.questions.length === 0 && (
                <GlassCard className="text-center py-12">
                  <ClipboardList className="h-16 w-16 text-gray-400 mx-auto mb-4" />
                  <h3 className="text-xl font-semibold text-white mb-2">
                    Start Building Your Survey
                  </h3>
                  <p className="text-gray-400 mb-6">
                    Add questions from the sidebar to get started
                  </p>
                </GlassCard>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen pt-20">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          className="text-center mb-12"
        >
          <h1 className="text-4xl md:text-6xl font-bold mb-6">
            <span className="gradient-text">Survey Generator</span>
            <br />
            <span className="text-white">Tool</span>
          </h1>
          <p className="text-xl text-gray-300 max-w-3xl mx-auto">
            Create custom surveys for market research, customer feedback, and data collection. 
            Use our templates or build from scratch with our intuitive drag-and-drop builder.
          </p>
        </motion.div>

        {/* Action Buttons */}
        <div className="flex flex-col sm:flex-row gap-4 justify-center mb-12">
          <Button size="lg" icon={Plus} onClick={startNewSurvey}>
            Create New Survey
          </Button>
          <Button variant="outline" size="lg" icon={ClipboardList}>
            Browse Templates
          </Button>
        </div>

        {/* Templates */}
        <div className="mb-12">
          <h2 className="text-2xl font-bold text-white mb-6">Survey Templates</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            {surveyTemplates.map((template, index) => (
              <motion.div
                key={template.id}
                initial={{ opacity: 0, y: 50 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: index * 0.1 }}
              >
                <GlassCard className="h-full cursor-pointer group" onClick={() => useTemplate(template)}>
                  <div className="flex items-center justify-between mb-3">
                    <div className="p-2 rounded-lg bg-gradient-to-r from-accent-blue to-accent-purple group-hover:scale-110 transition-transform duration-300">
                      <ClipboardList className="h-5 w-5 text-white" />
                    </div>
                    <span className="px-2 py-1 bg-accent-blue/20 text-accent-blue text-xs rounded-full">
                      {template.industry}
                    </span>
                  </div>
                  
                  <h3 className="text-lg font-semibold text-white mb-2 group-hover:text-accent-blue transition-colors duration-300">
                    {template.title}
                  </h3>
                  
                  <p className="text-gray-400 text-sm mb-4">
                    {template.description}
                  </p>
                  
                  <div className="flex items-center justify-between text-sm text-gray-400">
                    <span>{template.questions} questions</span>
                    <span className="text-accent-green">Use Template</span>
                  </div>
                </GlassCard>
              </motion.div>
            ))}
          </div>
        </div>

        {/* My Surveys */}
        {surveys.length > 0 && (
          <div>
            <h2 className="text-2xl font-bold text-white mb-6">My Surveys</h2>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {surveys.map((survey, index) => (
                <motion.div
                  key={survey.id}
                  initial={{ opacity: 0, y: 50 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: index * 0.1 }}
                >
                  <GlassCard className="h-full">
                    <div className="flex items-start justify-between mb-4">
                      <h3 className="text-lg font-semibold text-white">
                        {survey.title}
                      </h3>
                      <div className="flex space-x-2">
                        <button className="p-2 rounded-lg bg-white/5 hover:bg-white/10 transition-colors duration-300">
                          <Edit3 className="h-4 w-4 text-gray-400" />
                        </button>
                        <button className="p-2 rounded-lg bg-white/5 hover:bg-white/10 transition-colors duration-300">
                          <Share2 className="h-4 w-4 text-gray-400" />
                        </button>
                      </div>
                    </div>
                    
                    <p className="text-gray-400 text-sm mb-4">
                      {survey.description}
                    </p>
                    
                    <div className="flex items-center justify-between text-sm text-gray-400 mb-4">
                      <span>{survey.questions.length} questions</span>
                      <span>{survey.responses} responses</span>
                    </div>
                    
                    <div className="flex space-x-2">
                      <Button size="sm" className="flex-1" icon={Eye}>
                        View
                      </Button>
                      <Button variant="outline" size="sm" className="flex-1" icon={BarChart3}>
                        Results
                      </Button>
                    </div>
                  </GlassCard>
                </motion.div>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default SurveyGenerator;